<script>
import UniFooter from "@/components/footer";
import PlaceHolderImage from "../../assets/placeholder.png";
import { mapState } from "vuex";

const InitProduct = {
  title: "",
  subTitle: "",
  features: [],
  descriptions: [],
  applications: [],
  pictures: [],
};

export default {
  name: "Products",
  components: { UniFooter },
  data() {
    return {
      selectedProduct: "",

      product: { ...InitProduct },
      PlaceHolderImage,
    };
  },

  computed: {
    ...mapState({
      currentProduct: (state) => state.currentProduct,
    }),
  },

  watch: {
    currentProduct: {
      immediate: true,
      handler: function () {
        this.selectedProduct = this.currentProduct;
        this.handleSelectChange();
      },
    },
  },

  methods: {
    handleSelectChange() {
      const name = this.selectedProduct[this.selectedProduct.length - 1];
      if (!name) return;
      const product = this.i18n.productList[name];
      if (!product) {
        this.$message.warning(`未找到${name}的产品信息`);
        this.product = { ...InitProduct };
        return;
      }
      this.product = product;
    },
  },
};
</script>

<template>
  <div class="products">
    <!-- <div class="products-select">
      <el-cascader
        v-model="selectedProduct"
        :options="i18n.productNameOptions"
        :placeholder="i18n.productSelectTip"
        @change="handleSelectChange"
      >
      </el-cascader>
    </div> -->
    <div v-if="product.title">
      <div class="title products-section">{{ product.title }}</div>
      <div class="sub-title products-section">
        <div class="sub-title__item">
          {{ product.subTitle }}
        </div>
      </div>

      <div class="grid-2 products-section">
        <div class="pictures">
          <div class="item-title">{{ i18n.productPictureTip }}</div>
          <el-carousel height="400px" width="600px">
            <el-carousel-item v-for="p in product.pictures" :key="p">
              <el-image
                style="width: 600px; height: 400px"
                :src="'/' + p"
                fit="contain"
              ></el-image>
            </el-carousel-item>
          </el-carousel>
        </div>

        <div class="features">
          <div class="item-title">{{ i18n.productFeatureTip }}</div>
          <p class="features_item" v-for="f in product.features" :key="f">
            <i
              class="el-icon-check"
              style="color: #33a1d0; font-weight: bold; margin-right: 12px"
            ></i>
            <span>{{ f }}</span>
          </p>
        </div>
      </div>

      <div class="grid-2 products-section">
        <div class="descriptions products-section">
          <div class="item-title">{{ i18n.productDescTip }}</div>
          <p
            class="descriptions_item"
            v-for="d in product.descriptions"
            :key="d"
          >
            {{ d }}
          </p>
        </div>

        <div class="applications products-section">
          <div class="item-title">{{ i18n.productionApplicationTip }}</div>
          <p
            class="application_item"
            v-for="a in product.applications"
            :key="a"
          >
            {{ a }}
          </p>
        </div>
      </div>
      <div class="products-footer">
        <uni-footer />
      </div>
    </div>
    <div v-else class="placeholder-image">
      <el-image
        :src="PlaceHolderImage"
        style="width: 100%; max-width: 400px"
      ></el-image>
    </div>
  </div>
</template>

<style scoped lang="scss">
@import "../../style/global.scss";
.products {
  padding: 70px 0 120px;
  text-align: left;
  margin: 0 120px 0;
  font-size: 16px;

  &-select {
    margin-top: 24px;
    .el-cascader {
      width: 100%;
    }
  }

  &-section {
    margin-top: 32px;
  }

  .item-title {
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal;
    color: #4a4a4a;
    margin: 0;
    font-size: 24px;
    line-height: 1;

    position: relative;
    &::before {
      @extend .vertical-decoration;
    }
  }

  .title {
    font-size: 36px;
    font-weight: 700;
    position: relative;
    &::before {
      @extend .vertical-decoration;
    }
  }

  .sub-title {
    padding: 12px;
    border-radius: 8px;
    border: solid 1px #e5ebf2;
    position: relative;
    background-color: rgba(244, 247, 255, 0.85);

    &__item {
      font-size: 16px;
      font-weight: 700;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      color: #4a4a4a;
      padding-left: 10px;
      padding-right: 5px;
    }
  }

  .pictures {
    .el-carousel {
      width: 600px;
    }
  }

  .descriptions {
    width: 100%;
    border: solid 1px #e5ebf2;
    background-color: #f9fbff;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
    overflow: hidden;
    padding: 20px 20px 10px;
    overflow: hidden;
    color: #4c4c4c;
    line-height: 2;
  }

  .features {
    width: 100%;
    border: solid 1px #e5ebf2;
    background-color: #f9fbff;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
    overflow: hidden;
    padding: 20px 20px 10px;
    overflow: hidden;
  }

  .applications {
    width: 100%;
    border: solid 1px #e5ebf2;
    background-color: #f9fbff;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
    overflow: hidden;
    padding: 20px 20px 10px;
    overflow: hidden;
    font-weight: bold;
  }

  &-footer {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
  }

  .placeholder-image {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20%;
  }
}
</style>